type Usage = {
  id: number
  team: string
  instanceType: string
  usageHours: number
  costPerHour: number
  totalCost: number
  billingPeriod: string
  status: string
}

export default function UsagePage() {
  const [selectedInstanceType, setSelectedInstanceType] = useState<string>("all")
  const [selectedBillingPeriod, setSelectedBillingPeriod] = useState<string>("all")
  const [sorting, setSorting] = useState<SortingState>([])
  const [columnFilters, setColumnFilters] = useState<ColumnFiltersState>([])
  const [columnVisibility, setColumnVisibility] = useState<VisibilityState>({})
  const [rowSelection, setRowSelection] = useState({})
  const [pagination, setPagination] = useState<PaginationState>({
    pageIndex: 0,
    pageSize: 10,
  })

  const filteredData = useMemo(() => {
    return usageData.filter(item => {
      const matchesInstanceType = selectedInstanceType === "all" || item.instanceType === selectedInstanceType
      const matchesBillingPeriod = selectedBillingPeriod === "all" || item.billingPeriod === selectedBillingPeriod
      return matchesInstanceType && matchesBillingPeriod
    })
  }, [selectedInstanceType, selectedBillingPeriod])

  const table = useReactTable({
    data: filteredData,
    columns: [
      {
        accessorKey: "team",
        header: ({ column }) => {
          return (
            <Button
              variant="ghost"
              onClick={() => column.toggleSorting(column.getIsSorted() === "asc")}
              className="flex h-8 items-center justify-start p-0 font-medium"
            >
              Team
              <ArrowUpDown className="ml-2 h-4 w-4" />
            </Button>
          )
        },
        cell: ({ row }) => <div className="text-left">{row.getValue("team")}</div>
      },
      {
        accessorKey: "instanceType",
        header: "Instance Type",
        cell: ({ row }) => <div className="text-left">{row.getValue("instanceType")}</div>
      },
      {
        accessorKey: "usageHours",
        header: ({ column }) => {
          return (
            <div>
              <Button
                variant="ghost"
                onClick={() => column.toggleSorting(column.getIsSorted() === "asc")}
                className="flex h-8 items-center justify-start p-0 font-medium"
              >
                Usage Hours
                <ArrowUpDown className="ml-2 h-4 w-4" />
              </Button>
              <div className="text-xs text-muted-foreground mt-1">
                Sum: {filteredData.reduce((sum, item) => sum + item.usageHours, 0).toFixed(1)}
              </div>
            </div>
          )
        },
        cell: ({ row }) => <div className="text-left">{row.getValue("usageHours")}</div>
      },
      {
        accessorKey: "costPerHour",
        header: "Cost/Hour",
        cell: ({ row }) => <div className="text-left">${row.getValue("costPerHour")}</div>
      },
      {
        accessorKey: "totalCost",
        header: ({ column }) => {
          return (
            <div>
              <Button
                variant="ghost"
                onClick={() => column.toggleSorting(column.getIsSorted() === "asc")}
                className="flex h-8 items-center justify-start p-0 font-medium"
              >
                Total Cost
                <ArrowUpDown className="ml-2 h-4 w-4" />
              </Button>
              <div className="text-xs text-muted-foreground mt-1">
                Sum: ${filteredData.reduce((sum, item) => sum + item.totalCost, 0).toFixed(2)}
              </div>
            </div>
          )
        },
        cell: ({ row }) => {
          const amount = parseFloat(row.getValue("totalCost"));
          const formatted = new Intl.NumberFormat("en-US", {
            style: "currency",
            currency: "USD",
          }).format(amount);
          return <div className="text-left">{formatted}</div>;
        }
      },
      {
        accessorKey: "billingPeriod",
        header: "Billing Period",
        cell: ({ row }) => <div className="text-left">{row.getValue("billingPeriod")}</div>
      },
      {
        accessorKey: "status",
        header: "Status",
        cell: ({ row }) => <div className="text-left">{row.getValue("status")}</div>
      },
    ],
    onSortingChange: setSorting,
    onColumnFiltersChange: setColumnFilters,
    getCoreRowModel: getCoreRowModel(),
    getPaginationRowModel: getPaginationRowModel(),
    getSortedRowModel: getSortedRowModel(),
    getFilteredRowModel: getFilteredRowModel(),
    onColumnVisibilityChange: setColumnVisibility,
    onRowSelectionChange: setRowSelection,
    onPaginationChange: setPagination,
    state: {
      sorting,
      columnFilters,
      columnVisibility,
      rowSelection,
      pagination,
    },
  })

  // Get unique instance types and billing periods for filters
  const instanceTypes = [...new Set(usageData.map(item => item.instanceType))]
  const billingPeriods = [...new Set(usageData.map(item => item.billingPeriod))]

  return (
    <div className="container mx-auto py-10 px-4">
      <div className="flex gap-4 mb-6">
        <div className="w-[200px]">
          <Select value={selectedInstanceType} onValueChange={setSelectedInstanceType}>
            <SelectTrigger>
              <SelectValue placeholder="Select Instance Type" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Instance Types</SelectItem>
              {instanceTypes.map((type) => (
                <SelectItem key={type} value={type}>
                  {type}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
        <div className="w-[200px]">
          <Select value={selectedBillingPeriod} onValueChange={setSelectedBillingPeriod}>
            <SelectTrigger>
              <SelectValue placeholder="Select Billing Period" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Periods</SelectItem>
              {billingPeriods.map((period) => (
                <SelectItem key={period} value={period}>
                  {period}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
      </div>
      <Card className="p-0">
        <div className="rounded-md border">
          <Table>
            <TableHeader>
              {table.getHeaderGroups().map((headerGroup) => (
                <TableRow key={headerGroup.id}>
                  {headerGroup.headers.map((header) => {
                    return (
                      <TableHead key={header.id}>
                        {header.isPlaceholder
                          ? null
                          : flexRender(
                              header.column.columnDef.header,
                              header.getContext()
                            )}
                      </TableHead>
                    )
                  })}
                </TableRow>
              ))}
            </TableHeader>
            <TableBody>
              {table.getRowModel().rows?.length ? (
                table.getRowModel().rows.map((row) => (
                  <TableRow
                    key={row.id}
                    data-state={row.getIsSelected() && "selected"}
                  >
                    {row.getVisibleCells().map((cell) => (
                      <TableCell key={cell.id}>
                        {flexRender(
                          cell.column.columnDef.cell,
                          cell.getContext()
                        )}
                      </TableCell>
                    ))}
                  </TableRow>
                ))
              ) : (
                <TableRow>
                  <TableCell
                    colSpan={table.getAllColumns().length}
                    className="h-24 text-center"
                  >
                    No results.
                  </TableCell>
                </TableRow>
              )}
            </TableBody>
          </Table>
        </div>
        <div className="flex items-center justify-between space-x-2 py-4">
          <div className="flex-1 text-sm text-muted-foreground">
            {table.getFilteredSelectedRowModel().rows.length} of{" "}
            {table.getFilteredRowModel().rows.length} row(s) selected.
          </div>
          <div className="flex items-center space-x-2">
            <Button
              variant="outline"
              size="sm"
              onClick={() => table.previousPage()}
              disabled={!table.getCanPreviousPage()}
            >
              Previous
            </Button>
            <Button
              variant="outline"
              size="sm"
              onClick={() => table.nextPage()}
              disabled={!table.getCanNextPage()}
            >
              Next
            </Button>
          </div>
        </div>
      </Card>
    </div>
  )
}
